<template>
    <div class="user_info">
        <Navigator></Navigator>
        <userDetailTop :msg="msg" :iss="iss"></userDetailTop>
        <Right :msg="msg"></Right>
    </div>
</template>
<style lang="scss">
    .user_info{
        width:1200px;
        margin:0 auto;
    }
</style>
<script>
import Navigator from "../../components/navigator"
import userDetailTop from "../../components/userDetailTop"
import Right from "../../components/userDetailRight"
import url from "../../modules/baseURL"
const base64url = require('base64url');

export default {
    data(){
        return {
            jwt:"",
            payload:"",
            decode:"",
            iss:"",
            msg:""
        }
    },
    components:{
        Navigator,
        userDetailTop,
        Right
    },
    created(){
        // this.msg = require("../../assets/test.json").data;
        // var test = "eyJhbGciOiJFUzI1NmsiLCJ0eXAiOiJKV1QifQ==..NOKzlA3aaCwXHIbhKKX4fWO7J4Jd6PfKFVqeoN4_5nlxJEFXJTWgYasXXCdsA6eKgPhzPMACsJrwr6m8vLaEnRw=";
        // window.localStorage.setItem("token",test)
        var jwt = window.localStorage.getItem("token");
        this.payload = jwt.split(".")[1];
        this.jwt = jwt;
        this.decode =  JSON.parse(base64url.decode(this.payload));
        this.iss = this.decode.iss;

        this.$http.post(url.baseURL+"/thirdparty/identity_information",{
            jwt:this.jwt,
            identity:this.iss
        }).then(res=>{
            this.msg = res.data;
        })
    }
}
</script>